    export default {
        data() {
            return {
                paymentId : '',
                paymentResultStr : '',
                isSuccess : true
            }
        },
        onLoad(){
            this.paymentId = uni.getStorageSync('payment_id');
            uni.request({
                url: 'http://114.116.64.103:9000/get_payment_status',
                method: "POST",
                dataType:'json',
                header: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                },
                data: {
                    payment_id:this.paymentId
                },
                success: res => {
                    console.log(res);
                    if (res.data.msg === '0') {
                        this.paymentResultStr = '支付成功';
                        this.isSuccess = true;
                    }
                    else {
                        this.paymentResultStr = '支付失败';
                        this.isSuccess = false;
                    }
                },
                fail: (err) => {
                    console.log('request fail', err);
                },
                complete: () => {}
            });
        },
        methods: {
			backToMyInfo(){
				uni.switchTab({
					url:'../ucenter/my_info'
				})
			},
			goOn(){
				uni.switchTab({
					url:'pay_index'
				})
			}
		}
    }
